html, body {
	text-transform: uppercase;
	color: silver;
	background-color: black;
}

.noselect {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
scrollbar-width: auto;
scrollbar-color: #d4d4d4 #ffffff;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
	width: 0px;
}

.hApp, .h1, .h2, .h3, .h4, .h5, .h6, .heading {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    color: white;
    font-weight: 400;
}

/*
	Link
*/

.focus {
	color: white !important;
}

.link {
	display: block;
	padding: 3px 0;
	cursor: pointer;
	transition: 0.2s all;
	color: currentColor;
}

.link:hover {
	color: white;
	transform: scale(1.2);
}

.link:active {
	color: white;
	transform: scale(0.98);
}

.link-icon {
	margin-right: 12px;
}

.link-update {
	animation-timing-function: ease;
	animation-duration: 2s;
	animation-iteration-count: infinite;
	animation-name: bounce;
	transform-origin: bottom;
}

.link-update .link-icon {
	margin-right: 3px;
}

.fill {
	width: 100%;
	min-width: 100%;
	max-width: 100%;
	height: 100%;
	min-height: 100%;
	max-height: 100%;
}

.nav {
	position: relative;
	z-index: 15;
}

.hspace-out {
    margin-left: 36px;
    margin-right: 36px;
    width: calc(100% - 36px - 36px);
}

.hApp {
    font-size: 32px;
    letter-spacing: 6px;
}
.hApp:hover {
	color: white;
}

.app-title {
    height: 108px;
}

a {
	color: currentColor;
}
a:hover {
	color: currentColor;
	text-decoration: none;
	cursor: pointer;
}

.nav-content {
    height: calc(100% - 108px);
}

.nav-radios {
    height: calc(100% - 120px - 24px - 18px);
    overflow-y: scroll;
}

.nav-radios-stack {
    min-height: 100%;
    overflow: hidden;
}

.nav-support {
    margin-top: 24px;
    margin-bottom: 18px;
}

.youtube::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient( black, transparent, transparent, transparent, transparent, black );
    content: "";
    overflow: hidden;
    pointer-events: none;
}

.youtube-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.youtube-video iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 100vh;
    border: none;
    transform: translate(-50%, -50%);
    pointer-events: none;
}

iframe.ready {
	opacity: 1;
}

.youtube-controls {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 10;
    overflow: hidden;
}

.vspace-bottom {
    margin-bottom: 12px;
}
.vspace-top {
    margin-top: 18px;
}

.youtube-left-space {
    margin: 6px;
    width: calc(102px - 6px - 12px - 18px - 12px);
}

.youtube-volume {
    width: 102px;
    margin: 6px;
}

/*
	Control
*/

/* -----------------------------------------------------------------------------
// Control
------------------------------------------------------------------------------*/
.control {
	border: 1px solid currentColor;
	border-radius: 50%;
	padding: 12px;
	margin: 6px;
	outline: none;
	background-color: transparent;
	color: currentColor;
	cursor: pointer;
	transition: 0.2s all;
}

.control:hover {
	color: white;
	transform: scale(1.15);
}

.control:active {
	color: white;
	transform: scale(0.98);
}

.control-main {
	padding: 24px;
}

/*
	Input
*/

input {
	color: currentColor;
}

input[type="range"] {
	-webkit-appearance: none;
	margin: 18px 0;
	width: 100%;
	background: transparent;
}

input[type="range"]:focus {
	outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
	width: 100%;
	height: 2px;
	cursor: pointer;
	background: currentColor;
	border-radius: 12px;
	border: none;
}

input[type="range"]::-webkit-slider-thumb {
	height: 12px;
	width: 12px;
	border-radius: 50%;
	background: currentColor;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -5px;
}

input[type="range"]:hover::-webkit-slider-runnable-track {
	background: white;
}

input[type="range"]:hover::-webkit-slider-thumb {
	background: white;
}

input[type="range"]::-moz-range-track {
	width: 100%;
	height: 2px;
	cursor: pointer;
	background: currentColor;
	border-radius: 12px;
	border: none;
}

input[type="range"]::-moz-range-thumb {
	height: 12px;
	width: 12px;
	border-radius: 50%;
	background: currentColor;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -5px;
}

input[type="range"]:hover::-moz-range-track {
	background: white;
}

input[type="range"]:hover::-moz-range-thumb {
	background: white;
}

.radio-player, .radio-shell {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}

.glow {
    text-shadow: 1px 1px rgb(0 0 0 / 70%);
}

@media (max-aspect-ratio: 16 / 9)
{
	.youtube-video iframe {
		width: 177.78vh;
	}
}

@media (min-aspect-ratio: 16 / 9)
{
	.youtube-video iframe {
		height: 56.25vw;
	}
}